<template>
    <Doc>
        <H1>app</H1>
        <P><Strong>/src/app.json</Strong></P>
        <Code :code='`{
    "name": "my-vue-doc",
    "background": "BackgroundColor",
    "logo": "./static/icon.svg",
    "version": "v0.0.0",
    "subhead": "my",
    "describe": "myiviewmy",
    "link": [
        {
            "title": "",
            "path": "/",
            "primary": true
        },
        {
            "title": "",
            "path": "https://www.github.com/laden666666/my-vue-doc"
        }
    ],
    "menu": [
        {
            "title": "",
            "path": "#/"
        },
        {
            "title": "",
            "children": [
                {
                    "title": "",
                    "path": "#/Advances/Code"
                },
            ]
        }
    ]
}`' lang="json"></Code>
        <H2></H2>
        <P></P>
        <Instructions type="h3" :data="[{
            name: 'name',
            type: 'String',
            required: true,
            describe: '',
        }, {
            name: 'logo',
            type: 'String',
            describe: 'rulstaticindex.htmlurl',
        }, {
            name: 'version',
            type: 'String',
            describe: '',
        }, {
            name: 'subhead',
            type: 'String',
            describe: '',
        }, {
            name: 'describe',
            type: 'String',
            describe: '\\n',
        }]">
        </Instructions>
        
        <Instructions type="h3" :data="[{
            name: 'link',
            type: 'Array',
            describe: 'url',
        }]">
            <template slot="link">
                <P></P>
                <Code :code='`{
    "link": [
        {
            "title": "",
            "path": "/",
            "primary": true
        },
        {
            "title": "",
            "path": "https://www.github.com/laden666666/my-vue-doc"
        }
    ]
}`'></Code>
                <Instructions :data="[{
                    name: 'title',
                    type: 'String',
                    required: true,
                    describe: '',
                }, {
                    name: 'path',
                    type: 'String',
                    required: true,
                    describe: 'url#pages',
                }, {
                    name: 'primary',
                    type: 'Boolean',
                    required: false,
                    describe: '',
                }]"></Instructions>
            </template>
        </Instructions>

        
        
        <Instructions type="h3" :data="[{
            name: 'menu',
            type: 'Array',
            describe: 'url',
        }]">
            <template slot="menu">
                <P></P>
                <Code :code='`{
    "menu": [
        {
            "title": "",
            "children": [
                "title": "",
                "path": "/",
            ]
        },
        {
            "title": "",
            "path": "https://www.github.com/laden666666/my-vue-doc"
        }
    ]
}`'></Code>
                <Instructions :data="[{
                    name: 'title',
                    type: 'String',
                    required: true,
                    describe: 'pathpath',
                }, {
                    name: 'path',
                    type: 'String',
                    required: false,
                    describe: 'url#pages',
                }, {
                    name: 'children',
                    type: 'Array',
                    required: false,
                    describe: '',
                }]"></Instructions>
            </template>
        </Instructions>

        <H3></H3>
        <Demo title="app.js" :demo="AppConfig" lang="json" :code='`{
    "name": "my-vue-doc",
    "background": "BackgroundColor",
    "logo": "./static/icon.svg",
    "version": "v0.0.0",
    "subhead": "my",
    "describe": "myiviewmy",
    "link": [
        {
            "title": "",
            "path": "/",
            "primary": true
        },
        {
            "title": "",
            "path": "https://www.github.com/laden666666/my-vue-doc"
        }
    ],
}`'>
            <div >app.json</div>
        </Demo>

        <H2></H2>
        <P>app.json<Strong>background</Strong></P>

        <H3>BackgroundColor</H3>
        <P></P>
        <div class="bg">
            <BackgroundColor></BackgroundColor>
        </div>
        
        <H3>Particles</H3>
        <P><A href="https://github.com/VincentGarreau/particles.js">Particles</A></P>
        <div class="bg">
            <Particles></Particles>
        </div>
    </Doc>
</template>
<script>
import AppConfig from '../demo/AppConfig/AppConfig'
import BackgroundColor from '../components/Background/strategy/BackgroundColor'
import Particles from '../components/Background/strategy/Particles'
export default {
    data() {
        return {
            AppConfig
        }
    },
    components:{
        BackgroundColor,
        Particles,
    }
}
</script>
<style scoped>
    .bg{
        width: 400px;
        max-width: 100%;
        height: 200px;
        position: relative;
    }
</style>



